@import "vars";
@import "fonts";
@import "breakpoints";

body {
    font-family: $font_main;
    color: $color_black;
    min-width: $tel_xxs;
    margin: 0;
}

a {
    color: inherit;
    cursor: pointer;
    display: inline-block;
    outline: none !important;
    text-decoration: none;
    transition: all 0.15s ease-in-out;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);

    &::-moz-focus-inner {
        border: none;
    }
}

.wrapper-size {
    max-width: 1160px;
    margin: 0 auto;
    position: relative;
    height: 100%;
  
    @include media_tablet {
      padding: 0 30px;
    }
  
    @include media_tablet_vert {
      padding: 0 24px;
    }
  
    @include media_tel_lg {
      padding: 0 15px;
    }
  
    @include media_tel_xs {
      padding: 0 10px;
    }
  }
  


@mixin flexReset() {
    flex: 0 1 auto;
    max-width: none;
    width: auto;
}

@mixin inheritLink {
    a {
        color: inherit;
        text-decoration: none;
    }
}