@import "fonts";
@import "vars";
@import "basis";


.header-container {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    align-items: center;

    a,
    img {
        display: block;
    }

    & .header-container__logo {
        margin: 1.875rem 0;
        grid-column-start: 2;
        justify-self: center;
    }

    & .header-container__search {
        justify-self: end;
    }
}

.slide-toggle {
    transform: translateY(-100%);
    transition: all 0.5s ease-in-out;
}


.primary-nav {
    position: sticky;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 100;
    border-bottom: 1px solid $border;
    border-top: 1px solid $border;
    background-color: $color_white;
    transition: all 0.5s ease-in-out;

    @include media_tel {
        display: none;
    }

    & .menu {
        display: flex;
        justify-content: center;
        list-style: none;
        margin: 0;
    }

    & ul li {
        margin-left: 2rem;
        position: relative;
    }

    & li>a {
        font-size: 1rem;
        font-weight: 500;
        margin-right: 0.875rem;
    }

    & li.parent>a::after {
        content: '';
        background-image: url("../img/arrow-down.svg");
        background-repeat: no-repeat;
        height: 5px;
        width: 10px;
        position: absolute;
        right: 0;
        top: 50%;
        transform: translateY(-50%);
    }

    & .menu>li>a {
        padding: 1.25rem 0;
    }

    & .parent ul {
        display: none;
        list-style: none;
        padding: 1.25rem;
        position: absolute;
        left: 0;
        min-width: 176px;
        background-color: $color_white;
        border: 1px solid $border;
    }

    & .parent ul li {
        padding: 0.5rem 0;
        display: flex;
        justify-content: space-between;
        position: relative;
        margin: 0;
    }

    & .parent ul li:not(:last-child) {
        border-bottom: 1px solid $border;
    }

    & .parent ul li:after {
        content: '';
        background-image: url("../img/arrow-right.svg");
        background-repeat: no-repeat;
        height: 8px;
        width: 5px;
        position: absolute;
        right: 0;
        top: 50%;
        transform: translateY(-50%);
    }

    & .parent ul li a {
        font-size: 0.8125rem;
        font-weight: 400;
    }

    & .parent ul li a:hover {
        color: $color_grey_3;
        padding-left: 0.5rem;
    }

    & .parent:hover>ul {
        display: block;
    }
}

.header-container__burger {
    position: relative;
    width: 2.25rem;
    height: 1.25rem;
    cursor: pointer;
    display: none;

    @include media_tel {
        display: inline-block;
    }

    & span {
        margin: 0 auto;
        position: relative;
        width: 1.438rem;
        height: 0.25rem;
        background-color: $color_black;
        display: block;
        transition-duration: 0s;
        transition-duration: 0s;
        transition-delay: 0.2s;
        transition-delay: 0.2s;
        border-radius: 0.25rem;
    }

    & span::before {
        position: absolute;
        content: '';
        width: 1.438rem;
        height: 0.25rem;
        background-color: $color_black;
        display: block;
        margin-top: 0.384rem;
        border-radius: 0.25rem;
    }

    & span::after {
        position: absolute;
        content: '';
        width: 1.438rem;
        height: 0.25rem;
        background-color: $color_black;
        display: block;
        margin-top: -0.384rem;
        border-radius: 0.25rem;
    }
}

.posts {
    display: flex;
    flex-wrap: wrap;
    gap: 48px 40px;
    justify-content: center;
    margin: 3rem 0;

    @include media_tablet_vert {
        justify-content: center;
    }

    @include media_tel {
        @include flexReset;
    }

    &__item {
        max-width: 360px;

        @include media_tel {
            max-width: 335px;
        }

        &-image {

            a,
            img {
                display: block;
            }

            img {
                height: auto;
                padding: 0;

                @include media_tel {
                    width: 335px;
                    height: 230px;
                }
            }

        }

        &-desc {
            margin-top: 1rem;
        }

        &-tags {
            font-size: 0.8125rem;
            line-height: 1.25rem;
            font-weight: 700;

            a {
                color: $color_red;
            }
        }

        &-title {
            margin-top: 1rem;
            font-size: 1.5rem;
            line-height: 2rem;
            font-weight: 700;
            @include inheritLink;
        }

        &-info {
            margin-top: 1rem;
            display: flex;
            align-items: baseline;
            color: $color_grey;
            font-size: .75rem;
            line-height: 1rem;

            &>*+* {
                position: relative;
                margin-left: 0.313rem;
                padding-left: 0.5rem;

                &:before {
                    content: '';
                    position: absolute;
                    left: 0;
                    top: 50%;
                    transform: translateY(-50%);
                    width: 3px;
                    height: 3px;
                    border-radius: 50%;
                    background-color: $color_grey_2;
                }
            }
        }

        &-author {
            font-weight: 500;
            color: $color_black;
        }

        &-intro {
            margin-top: 1rem;
            color: $color_grey;
            font-size: .875rem;
            line-height: 1.125rem;
        }
    }
}

.mobile-menu {
    position: fixed;
    top: 0;
    left: 0;
    transition: all .5s ease-in-out;
    transform: translateX(-101%);
    z-index: 102;
    background-color: $color_white;
    height: 100%;
    min-width: 320px;
    border-right: 1px solid $border;

    &.active {
        transform: translateX(0);
        display: block;
    }

    &+.mobile-menu__overlay {
        position: absolute;
        background: rgba(255, 255, 255, 0.6);
        width: 100%;
        height: 100%;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        z-index: 100;
        display: none;
    }

    & .mobile-menu__header {
        padding: 2rem 1.25rem 1.563rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
        border-bottom: 1px solid $border;
    }

    & .mobile-menu__close {
        margin-left: 0.625rem;
        width: 20px;
        height: 20px;

        & span {
            position: relative;
            width: 20px;
            height: 20px;
            display: block;
        }

        & span::before {
            position: absolute;
            content: "";
            height: 20px;
            width: 4px;
            right: 50%;
            background-color: $color_black;
            transform: rotate(45deg);
            border-radius: 4px;
        }

        & span::after {
            position: absolute;
            content: "";
            height: 20px;
            width: 4px;
            right: 50%;
            background-color: $color_black;
            transform: rotate(-45deg);
            border-radius: 4px;
        }
    }

    &.active+.mobile-menu__overlay {
        display: block;
    }

    & ul {
        list-style: none;
        margin: 0;
        padding: 0 1.25rem;
    }

    & ul li {
        padding: 1rem 0;
        position: relative;
        cursor: pointer;
    }

    & li>a {
        font-size: 1rem;
        font-weight: 500;
        margin-right: 0.875rem;
    }

    & li.parent {
        border-bottom: 1px solid $border;
    }

    & li.parent>a::after {
        content: '';
        background-image: url("../img/arrow-down.svg");
        background-repeat: no-repeat;
        height: 5px;
        width: 10px;
        position: absolute;
        right: 0;
        top: 20px;
        transform: translateY(-50%);
        transition: max-height 0.6s ease-in-out;
    }

    & .parent ul li:after {
        content: '';
        background-image: url("../img/arrow-right.svg");
        background-repeat: no-repeat;
        height: 8px;
        width: 5px;
        position: absolute;
        right: 0;
        top: 50%;
        transform: translateY(-50%);
    }

    & .parent ul {
        max-height: 0;
        overflow: hidden;
        transition: max-height 0.4s ease-in-out;

    }
    
    & .parent ul li a {
        font-size: 0.8125rem;
        font-weight: 400;
    }

    & .parent ul li:not(:last-child) {
        border-bottom: 1px solid #e9e9e9;
    }
    
        

    & .parent.active ul {
        max-height: 1000px;
        transition: max-height 0.6s ease-in-out;
    }
}